* {
  margin: 0;
  padding: 0; }

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale; }

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New", monospace; }

img {
  width: 10vh; }

.app-container {
  width: 100vw;
  height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  background: #a8a8a8; }

.display-container {
  width: 30vh;
  min-height: 18vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  background: lightgray;
  padding: 1vh;
  border-radius: 2vh; }
  .display-container button {
    margin: 0.5vh;
    padding: 0.5vh;
    color: white;
    background: black;
    border: 5px solid white;
    outline: none;
    border-radius: 15px; }
    .display-container button:hover {
      cursor: pointer;
      color: black;
      background: white;
      border: 5px solid black; }
    .display-container button:disabled {
      cursor: default;
      color: gray;
      background: lightgray;
      border: 5px solid gray; }

.time-container {
  display: flex;
  margin: 0.5vh; }
  .time-container button {
    margin: 0 0 0 1.5vh; }

.garage-container {
  width: 20vh;
  height: 50vh;
  background: #ffc400;
  padding: 0 0 1vh 0;
  margin: 5vh; }

.occupied-spot {
  display: flex;
  justify-content: center;
  width: 19vh;
  height: 7.35vh;
  background: #a8a8a8;
  margin: 1vh 0 1vh 1vh;
  cursor: pointer; }
  .occupied-spot:hover {
    box-shadow: 0px 1px 5px black; }
  .occupied-spot:focus {
    outline: 0; }

.selected-spot {
  display: flex;
  justify-content: center;
  width: 19vh;
  height: 7.35vh;
  background: #a8a8a8;
  margin: 1vh 0 1vh 1vh;
  cursor: pointer;
  background: #a274d8; }
  .selected-spot:hover {
    box-shadow: 0px 1px 5px black; }

.vacant-spot {
  display: flex;
  justify-content: center;
  width: 19vh;
  height: 7.35vh;
  background: #a8a8a8;
  margin: 1vh 0 1vh 1vh;
  cursor: pointer;
  cursor: default; }

